body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logout-button-container{
  min-height: 50px;
}

.compare-toggle-container {
  min-height: 50px;
  padding-left: 5px;
  display: flex;
  align-items: center;
}

.compare-button {
  min-height: 50px;
  min-width: 120px;
  color: rgba(226, 148, 2, 0.678);
  background: none;
  background-color: none;
  border: none;
  transition: background-color .5s ease, color .5s ease;
}

.compare-button:hover {
  color: rgba(255, 255, 255, 0.836);
  background-color: rgba(226, 148, 2, 0.836);
}

.logout-button {
  min-height: 50px;
  min-width: 100px;
  background: none;
  background-color: none;
  color: rgba(255, 0, 0, 0.671);
  border: none;
  padding-left: 20px;
  padding-right: 20px;
  transition: background-color .5s ease, color .5s ease;
 
}

.user-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.username {
  padding-right: 20px;
}

.logout-button:hover {
  background-color: rgba(255, 0, 0, 0.527);
  color: rgba(255, 255, 255, 0.822);
}
.qb-row {
  margin: 0 0 10px 0;
  width: 95%;
  padding: 5px 10px;
  padding-top: 10px!important;
  padding-bottom: 10px!important;
  border: 1px solid rgba(223, 145, 0, 0.459);
  box-shadow: 2px 2px 5px #140d1d;
  min-height: 60px;
  display: flex;
  align-items: center;
  direction: ltr;
}

.drag-color {
  background-color: black;
}

.save-qb-button {
  background-color: rgba(85, 33, 105, 0.952);
  color: rgba(255, 255, 255, 0.616);
  border: 1px solid rgba(255, 255, 255, 0.37);
  padding: 5px 10px;
}

.saved-qb-button {
  background-color: rgba(155, 0, 0, 0.952);
  color: rgba(255, 255, 255, 0.801);
  border: 1px solid rgba(255, 255, 255, 0.13);
  padding: 5px 10px;
  font-size: .85em;
}

.qb-img-prev {
  height: 50px;
}

.qb-img {
  height: 100px;
}

.qb-img-container {
  display: inline-block;
  background-color: #2a262eaf;
  border: 1px solid #1310168f;
}
.qb-list {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 100%;
  max-height: 89.5vh;
  min-height: 89.5vh;
  padding: 3% 0px 0px 5%;
}

::-webkit-scrollbar {
  width: 3px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(226, 148, 2); 
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #19171b;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: black; 
}
.img-creator {
  padding: 2%;
  border: 2px solid rgba(223, 145, 0, 0.459);
  display: inline-block;
  width: 150px;
  
}

.title {
  font-size: 8em;
  letter-spacing: 8px;
  margin-bottom: 30px;
}
.welcome {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  min-height: 89.5vh;
  max-height: 89.5vh;
  overflow-y: scroll;
  overflow-x: hidden;
  direction: rtl;
}

.welcome-row{
  padding-top: 50px;
  padding-bottom: 50px;
}

.qb-info-list {
  direction: ltr;
  height: 100%;
  max-height: 89.5vh;
  min-height: 89.5vh;
  padding: 3% 0px 0px 5%;
}

.welcome-text-container {
  padding-left: 5%;
  padding-right: 5%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.comparea-list {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 100%;
  max-height: 89.5vh;
  min-height: 89.5vh;
  padding: 7% 0px 0px 5%;
  direction: rtl;
}

.compare-nav {
  background-color: #4d381d1e;
  position: fixed;
  top: 50px;
  height: 50px;
  width: 100%;
  
  transition: background-color .2s ease;
}

.compare-nav:hover {
  background-color:#E29402;
}

.dropdown > .dropdown-menu > li > a:hover {
  background-color: rgba(255, 255, 255, 0.705);
  color: black!important;
  background-image: none;
}

.dropdown > .dropdown-menu > li > a {
  color: rgba(255, 255, 255, 0.849);
  z-index: 2;
}

.dropdown > .dropdown-menu {
  margin-left: 20px;
  margin-top: 15px;
  width: 200px;
  border-radius: 0;
  border: 1px solid rgba(255, 255, 255, 0.432);
  background-color: #312A38;
  color: white;
  background-image: none;
  padding: 0;
}

.compare-form, .dropdown > button, .dropdown > button:active, .dropdown > button:hover, .dropdown > button:focus {
  width: 200px;
  position: relative;
  margin-left: 20px;
  margin-top: 10px;
  border-radius: 0;
  color: rgba(255, 255, 255, 0.863)!important;
  background-image: none!important;
  border: 1px solid rgba(255, 255, 255, 0.781); 
  background-color: #312A38!important;
  text-shadow: none;
}



