.data-left {
  border-right: 3px solid rgb(226, 148, 2);
}

.data-border {
  position: fixed;
  top: auto;
  left: auto;
  right: auto;
  bottom: auto;
  margin-left: auto;
  margin-right: auto;
}

.data-right {
  border-left: 3px solid rgb(226, 148, 2);
}

.navbar-fixed-top {
  box-shadow: 0px 5px 8px rgba(27, 17, 37, 0.705);
}

.nav-bot {
  box-shadow: 0px -5px 8px rgba(27, 17, 37, 0.705);
}


.h-100 {
  height: 100%;
}

.info-section {
  height: 100%;
  min-height: 89.5vh;
  max-height: 89.5vh;
  overflow-y: scroll;
  overflow-x: hidden;
  direction: rtl;
